<template>
  <div class="slider">
    <div class="images">
      <transition name="fade" mode="in-out">
        <div :class="currentImageClass" :key="currentImageClass"></div>
      </transition>
    </div>
    <div class="overlay"></div>
  </div>
</template>

<script>

  export default {
    name: 'HelloWorld',
    props: {
      msg: String
    },
    data() {
      return {
        currentImageIndex: 1,
        imageCount: 3
      }
    },
    mounted() {
      this.initSlider()
    },
    computed: {
      currentImageClass() {
        return `image-${this.currentImageIndex}`
      }
    },
    methods: {
      initSlider() {
        if (this.currentImageIndex < this.imageCount) {
          this.currentImageIndex++;
        } else {
          this.currentImageIndex = 1;
        }
        setTimeout(this.initSlider, 15 * 1000)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">


  .slider {
    position: relative;
  }

  .images {
    position: relative;
    z-index: 1;
    height: 100vh;

    & > div {
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 0;
      left: 0;
    }

    .images(@n, @i: 1) when (@i =< @n) {
      .image-@{i} {
        background: url('../assets/@{i}.jpg') no-repeat center center;
        background-size: cover;
      }
      .images(@n, (@i + 1));
    }

    .images(3);

  }

  .overlay {
    background: rgba(0, 0, 0, 0.45) url(data:image/svg+xml;base64,);
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity 2s ease-in-out;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
  }
</style>
